














import Vue from 'vue'
export default Vue.extend({
  methods: {
    showimage(){
      let uploadbox = <HTMLInputElement>document.getElementById('upload');
      let res = <HTMLImageElement>document.getElementById('show');
      let res1 = <HTMLCanvasElement>document.getElementById('show1');
      let ctx = res1.getContext('2d');
      if(uploadbox.files.length){
        let reader = new FileReader();
        let file = uploadbox.files[0];
        let img = new Image();
        img.src = URL.createObjectURL(file);
        
        img.onload = ()=>{
          ctx.drawImage(img, 0, 0);
        }
        // reader.onload = (ev)=>{
        //   // res.src = <string>ev.target.result;
        // };
        // reader.onloadend = ()=>{
        //   ctx.drawImage(res, 0, 0);
        // }
        // reader.readAsDataURL(file);
      }
    }
  }
})
